<template><undefined>
<h1>Affix 固钉</h1>
<p>将页面元素钉在可视范围。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>当内容区域比较长，需要滚动页面时，这部分内容对应的操作或者导航需要在滚动范围内始终展现。常用于侧边菜单和按钮组合。</li>
<li>页面可视范围过小时，慎用此功能以免遮挡页面内容。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-affix&gt;\n      &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary\&quot;&gt;&lt;span&gt;固定在顶部2&lt;/span&gt;&lt;/button&gt;\n  &lt;/v-affix&gt;&lt;br&gt;\n  &lt;v-affix :offset-bottom=\&quot;0\&quot;&gt;\n      &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary\&quot;&gt;&lt;span&gt;固定在底部&lt;/span&gt;&lt;/button&gt;\n  &lt;/v-affix&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-affix>
      <button type="button" class="ant-btn ant-btn-primary"><span>固定在顶部2</span></button>
  </v-affix><br>
  <v-affix :offset-bottom="0">
      <button type="button" class="ant-btn ant-btn-primary"><span>固定在底部</span></button>
  </v-affix>
</template>
</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在顶部2<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span> <span class="hljs-attr">:offset-bottom</span>=<span class="hljs-string">"0"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在底部<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 基本
  最简单的用法。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在顶部2<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span> <span class="hljs-attr">:offset-bottom</span>=<span class="hljs-string">"0"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在底部<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-affix :offset-top=\&quot;120\&quot; @change=\&quot;onChange\&quot;&gt;\n      &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn\&quot;&gt;&lt;span&gt;固定在距离顶部 120px 的位置&lt;/span&gt;&lt;/button&gt;\n  &lt;/v-affix&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    methods: {\n        onChange: affixed =&gt; console.log(affixed)\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-affix :offset-top="120" @change="onChange">
      <button type="button" class="ant-btn"><span>固定在距离顶部 120px 的位置</span></button>
  </v-affix>
</template>


</div>
                  <div slot="description"><h4 id="gu-ding-zhuang-tai-gai-bian-de-shi-jian"><a class="header-anchor" href="#gu-ding-zhuang-tai-gai-bian-de-shi-jian" aria-hidden="true">¶</a> 固定状态改变的事件</h4>
<p>可以获得是否固定的状态。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span> <span class="hljs-attr">:offset-top</span>=<span class="hljs-string">"120"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">'onChange'</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在距离顶部 120px 的位置<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        <span class="hljs-attr">onChange</span>: <span class="hljs-function"><span class="hljs-params">affixed</span> =&gt;</span> <span class="hljs-built_in">console</span>.log(affixed)
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 固定状态改变的事件
  可以获得是否固定的状态。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-affix</span> <span class="hljs-attr">:offset-top</span>=<span class="hljs-string">"120"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">'onChange'</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>固定在距离顶部 120px 的位置<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">v-affix</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        <span class="hljs-attr">onChange</span>: <span class="hljs-function"><span class="hljs-params">affixed</span> =&gt;</span> <span class="hljs-built_in">console</span>.log(affixed)
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="affix-props"><a class="header-anchor" href="#affix-props" aria-hidden="true">¶</a> Affix Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>offsetTop</td>
<td>距离窗口顶部达到指定偏移量后触发</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>offsetBottom</td>
<td>距离窗口底部达到指定偏移量后触发</td>
<td>Number</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="affix-events"><a class="header-anchor" href="#affix-events" aria-hidden="true">¶</a> Affix Events</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>固定状态改变时触发的事件</td>
<td>affixed</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
export default {
    methods: {
        onChange: affixed => console.log(affixed)
    }
}
</script>


